.profileInfo {
	.userInfo {
        padding-top: 20px;
        display: flex;
        row-gap: 10px;
        margin: auto;
        align-items: center;
        flex-direction: column;
        width: 80%;

		.content {
			border: 1px solid #16567A;
			width: 100%;
			height: 60vh;
			box-shadow: 3px 3px 5px 5px grey;
			display: flex;
			flex-direction: column;

			h3 {
                color :#16567A;
                align-self: center;
			}

			p {
				padding-left: 25px;
			}
		}

		button {
            height: 40px;
            width: fit-content;
            align-items: center;
            border-radius: 13px;
            border-color: #16567A;
            color: white;
            background-color: #16567A;
            
        }
	}
}